import { getCandidateData } from "./resumesourceAction";
import * as types from "./resumesourceTypes";

const INITIAL_STATE = {
  candidateData: [],
  isCandidateAdded: false,
  searchCandidateData: [],
  isSearchCandidateData: false,
  getCandidateData: [],
  isGetCandidateData: false,
  isdeleteCandidateData: false,
  isCandidateByNameData: false,
  getCandidateByNameData: [],
  isCandidateByIdDataArrived: false,
  getCandidateByIdData: [],
  isCandidateUpdated: false,
  dateFilterData: [],
  isDateFilterData: false,
  updatedcanddata: [],
  submitData: [],
  candidateDataSubmitted: false,
  flag: 0,
  addedDetailList: [],
  isAddedDetail: false,
  candidateBulkUpdated: false,
  csvList:[],
  getCsvList:false
};

export function resumesourceReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_CANDIDATE:
      return {
        ...state,
        isCandidateAdded: false,
      };

    case types.CREATE_CANDIDATE_SUCCESS:
      return {
        ...state,
        isCandidateAdded: true,
        candidateData: action.payload,
      };

    case types.CREATE_CANDIDATE_ERROR:
      return {
        ...state,
        isCandidateAdded: false,
      };

    case types.GET_CANDIDATE:
      return {
        ...state,
        isGetCandidateData: false,
      };

    case types.GET_CANDIDATE_SUCCESS:
      return {
        ...state,
        getCandidateData: action.payload,
        isGetCandidateData: true,
      };

    case types.GET_CANDIDATE_ERROR:
      return {
        ...state,
        isGetCandidateData: false,
      };

    case types.SEARCH_CANDIDATE:
      return {
        ...state,
        isSearchCandidateData: false,
      };

    case types.SEARCH_CANDIDATE_SUCCESS:
      return {
        ...state,
        isSearchCandidateData: true,
        getCandidateData: action.payload,
      };

    case types.SEARCH_CANDIDATE_ERROR:
      return {
        ...state,
        isSearchCandidateData: false,
      };

    case types.DELETE_CANDIDATE:
      return {
        ...state,
        isdeleteCandidateData: false,
      };

    case types.DELETE_CANDIDATE_SUCCESS:
      return {
        ...state,
        deleteCandidateData: action.payload,
        isdeleteCandidateData: true,
      };

    case types.DELETE_CANDIDATE_ERROR:
      return {
        ...state,
        isdeleteCandidateData: false,
      };

    case types.GET_CANDIDATE_BY_ID:
      return {
        ...state,
        isCandidateByIdDataArrived: false,
      };

    case types.GET_CANDIDATE_BY_ID_SUCCESS:
      return {
        ...state,
        isCandidateByIdDataArrived: true,
        getCandidateByIdData: action.payload.data,
      };

    case types.GET_CANDIDATE_BY_ID_ERROR:
      return {
        ...state,
        isCandidateByIdDataArrived: false,
        getCandidateByIdData: [],
      };
    case types.UPDATE_CANDIDATE:
      return {
        ...state,
        isCandidateUpdated: false,
        updatedcanddata: [],
      };

    case types.UPDATE_CANDIDATE_SUCCESS:
      return {
        ...state,
        isCandidateUpdated: true,
        updatedcanddata: action.payload,
      };

    case types.UPDATE_CANDIDATE_ERROR:
      return {
        ...state,
        isCandidateUpdated: false,
        updatedcanddata: [],
      };

    case types.FILTER_BY_DATE:
      return {
        ...state,
        isDateFilterData: false,
      };

    case types.FILTER_BY_DATE_SUCCESS:
      return {
        ...state,
        getCandidateData: action.payload,
        isDateFilterData: true,
      };

    case types.FILTER_BY_DATE_ERROR:
      return {
        ...state,
        isDateFilterData: false,
      };
    case types.SUBMIT_CANDIDATE:
      return {
        ...state,
        candidateDataSubmitted: false,
      };

    case types.SUBMIT_CANDIDATE_SUCCESS:
      return {
        ...state,
        candidateDataSubmitted: true,
        submitData: action.payload,
      };

    case types.SUBMIT_CANDIDATE_ERROR:
      return {
        ...state,
        candidateDataSubmitted: false,
      };

    case types.ADDED_DETAILS_LIST:
      return {
        ...state,
        addedDetailList: [],
        isAddedDetail: false,
      };

    case types.ADDED_DETAILS_LIST_SUCCESS:
      return {
        ...state,
        addedDetailList: action.payload,
        isAddedDetail: true,
      };

    case types.ADDED_DETAILS_LIST_ERROR:
      return {
        ...state,
        addedDetailList: [],
        isAddedDetail: false,
      };
    case types.UPDATE_CANDIDATE_BULK:
      return {
        ...state,
        candidateBulkUpdated: false,
      };

    case types.UPDATE_CANDIDATE_BULK_SUCCESS:
      return {
        ...state,
        candidateBulkUpdated: true,
        updatedcanddata: action.payload,
      };

    case types.UPDATE_CANDIDATE_BULK_ERROR:
      return {
        ...state,
        candidateBulkUpdated: false,
      };
      case types.GET_CSV:
        return {
          ...state,
          getCsvList: false,
        };
  
      case types.GET_CSV_SUCCESS:
        return {
          ...state,
          csvList: action.payload,
          getCsvList: true,
        };
  
      case types.GET_CSV_ERROR:
        return {
          ...state,
          getCsvList: false,
        };

    default:
      return state;
  }
}
